@import '../config/colours';
@import '../config/dimensions';

.main-map {
  .map-container {
    bottom: 0;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;

    & > .leaflet-map {
      height: 100%;

      &.leaflet-container {
        background: $map-bg;  // override leaflet's setting
      }
    }
  }
}



